.slideshow-container {
  position: relative;
  width: 100%;
  height: 100vh;
  margin: auto;
}

.slideshow-container::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-image: inherit;
  background-size: inherit;
  background-position: inherit;
  z-index: 1;
}

.slideshow-container::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  z-index: 2;
  background: linear-gradient(
    to bottom,
    rgba(4, 21, 45, 0.8) 0%,
    rgba(4, 21, 45, 0.6) 30%,
    rgba(4, 21, 45, 0.6) 50%,
    rgba(4, 21, 45, 1) 100%
  );
}

.mySlides {
  display: none;
  width: 100%;
  height: 100%;
}

.mySlides.active {
  display: block;
}

.text {
  display: grid;
  align-items: center;
  justify-content: center;
  margin-top: -500px;
}

.text h1 {
  color: #f2f2f2;
  font-size: 90px;
  font-weight: 600;
  text-transform: capitalize;
  z-index: 5;
  font-family: "Inter", sans-serif;
  text-align: center;
  padding: 5px;
}

